//  "Submit"  
.submit-btn {
   //  
   display: block;
   width: 114px;
   height: 42px;

   @include fonts($fw: 600, $fs: 12px, $lh: 15px);
   font-style: normal;

   color: getColor(txt-black);
   background-color: getColor(btn-default);
   border: 2px solid transparent;
   border-radius: 10px;
   @include animation(color);
   @include animation(background-color);

   // ,  
   @media screen and (min-width: 480px) {
      @include fonts($fw: 600, $fs: 14px, $lh: 17px);
      width: 121px;
   }
   &:hover {
      background-color: getColor(btn-hover);
   }
   &:focus {
      color: getColor(txt-white);
      background-color: getColor(btn-focus);
   }
   &:disabled {
      background-color: getColor(btn-disabled);
   }
}
